import React from 'react'
import style from './Skills.module.scss';
import Skill from "./skill/Skill";
import Title from "../common/components/title/Title";
import Fade from "react-reveal/Fade";


const Skills = () => {
    return (
        <Fade>
        <div  id={'skills'} className={style.skillsBlock}>
            <div className={style.skillsContainer}>
                <Title title={'Skills'} />
                <div className={style.skills}>
                    <Skill title={"HTML/CSS"} description={"Now I am improving my skills in this direction andexpanding them with new technologies. Open for your suggestions."}/>
                    <Skill title={"JavaScript/TypeScript"} description={"Now I am improving my skills in this direction andexpanding them with new technologies. Open for your suggestions."}/>
                    <Skill title={"React/redux/redux-toolkit"} description={"Now I am improving my skills in this direction andexpanding them with new technologies. Open for your suggestions."}/>
                    <Skill title={"React-hook-form/Formik"} description={"Now I am improving my skills in this direction andexpanding them with new technologies. Open for your suggestions."}/>
                    <Skill title={"Storybook"} description={"Now I am improving my skills in this direction andexpanding them with new technologies. Open for your suggestions."}/>
                    <Skill title={"Material UI"} description={"Now I am improving my skills in this direction andexpanding them with new technologies. Open for your suggestions."}/>
                    <Skill title={"AXIOS"} description={"Now I am improving my skills in this direction andexpanding them with new technologies. Open for your suggestions."}/>
                    <Skill title={"Git"} description={"Now I am improving my skills in this direction andexpanding them with new technologies. Open for your suggestions."}/>
                </div>
            </div>
        </div>
            </Fade>
    );
};

export default Skills;